.container.testimonials__container {
  width: 40%;
  padding-bottom: 4rem;
}
.client__avater {
  width: 4rem;
  aspect-ratio: 1/1;
  overflow: hidden;
  border-radius: 50%;
  margin: 0 auto 1rem;
  border: 0.4rem solid var(--color-primary-variant);
}
.testimonial {
  background: var(--color-bg-variant);
  text-align: center;
  padding: 2rem;
  border-radius: 2rem;
  user-select: none;
}
.client__review {
  color: var(--color-light);
  font-weight: 300;
  display: block;
  width: 80%;
  margin: 0.8rem;
}
.swiper-pagination-clickable .swiper-pagination-bullet {
  background: var(--color-primary-variant);
}
/*==============MEDIA QUERIES(tabltes)===========*/
@media screen and (max-width: 1024px) {
  .container.testimonials__container {
    width: 60%;
  }
}

/*==============MEDIA QUERIES(mobile)===========*/
@media screen and (max-width: 600px) {
  .container.testimonials__container {
    width: var(--container-width-sm);
  }
  .client__review {
    font-size: 1.3rem;
  }
  .client__name {
    font-size: 1.5rem;
  }
  .client__avater {
    width: 5rem;
  }
}
.client__review {
  width: var(--container-width-sm);
}
h5 {
  font-size: 1.5rem;
}
